export const freeTransactionSchemaFAQ = {
  "@context": "http://schema.org",
  "@graph": [
    {
      "@type": "FAQPage",
      mainEntity: [
        {
          "@type": "Question",
          name: "What is a foreign transaction fee?",
          acceptedAnswer: {
            "@type": "Answer",
            text:
              "<p>Financial institutions charge customers foreign transaction fees for using their credit or debit card for purchases in foreign currencies. The fee is typically 3% of each transaction total. It applies to purchases made abroad and online transactions made within your home country if the merchant processes it in a different currency. </p>",
            dateCreated: "2019-11-04T16.15Z",
            url: "https://brex.com/solution/fee-free-foreign-transaction/",
            author: {
              "@type": "Organization",
              name: "Brex",
            },
          },
        },
        {
          "@type": "Question",
          name: "How much are foreign transaction fees?",
          acceptedAnswer: {
            "@type": "Answer",
            text:
              "<p>Typically, foreign transaction fees are 3% of each transaction total made on your credit or debit card in a foreign currency.</p><p>Although it may not sound significant, a 3% charge can quickly add up, especially considering fees apply every time you use your card for purchases in different currencies. </p>",
            dateCreated: "2019-11-04T16.16Z",
            url: "https://brex.com/solution/fee-free-foreign-transaction/",
            author: {
              "@type": "Organization",
              name: "Brex",
            },
          },
        },
        {
          "@type": "Question",
          name: "Is it cheaper to use a credit card or cash abroad?",
          acceptedAnswer: {
            "@type": "Answer",
            text:
              "<p>Using your credit card abroad can be the most affordable option, as it allows you to access more accurate exchange rates. This is especially true if you choose a credit card with no foreign transaction fees. You'll also benefit from greater consumer protection. Your liability for credit card fraud is only $50, and many issuers have zero-liability policies.</p><p>You may need cash to make certain purchases abroad. Withdrawing cash from ATMs is typically less expensive than converting money before you travel. This method can be more costly than credit card purchases due to ATM fees, exchange rates and transaction fees. </p>",
            dateCreated: "2019-11-04T16.17Z",
            url: "https://brex.com/solution/fee-free-foreign-transaction/",
            author: {
              "@type": "Organization",
              name: "Brex",
            },
          },
        },
        {
          "@type": "Question",
          name: "Is it better to pay in local currency on credit card?",
          acceptedAnswer: {
            "@type": "Answer",
            text:
              "<p>You have the option to use your credit card's dynamic currency conversion (DCC) feature for transactions abroad. This allows you to make purchases in your home country's currency instead of a foreign one. </p><p>DCC makes it easier to understand foreign prices, but higher fees and poorer exchange rates make it more expensive. Your issuer will also still charge you a foreign transaction fee. This feature is optional so if you don’t want to pay more, request that the merchant charges your credit card in the local currency.  </p>",
            dateCreated: "2019-11-04T16.18Z",
            url: "https://brex.com/solution/fee-free-foreign-transaction/",
            author: {
              "@type": "Organization",
              name: "Brex",
            },
          },
        },
        {
          "@type": "Question",
          name: "What happens if my card gets blocked abroad?",
          acceptedAnswer: {
            "@type": "Answer",
            text:
              "<p>Tell your card issuer you are going abroad before you travel. They can add a note to your account, so they don't flag transactions from the country you are visiting as abnormal activity. Unfortunately, some issuers may still block your card. So, take their contact details with you, as well as a different payment card if possible.</p><p>Contact your issuer as soon as you can. They should unblock the card once you confirm your identity.</p>",
            dateCreated: "2019-11-04T16.19Z",
            url: "https://brex.com/solution/fee-free-foreign-transaction/",
            author: {
              "@type": "Organization",
              name: "Brex",
            },
          },
        },
      ],
    },
  ],
};

