/* eslint-disable */
import React from "react";

// JSON-LD Schema to apply structure to key areas on the website
// Doesn't necessarily have to be appended on the head, can be added to body

const JsonSchema = ({ data }) => (
  <script
    type="application/ld+json"
    dangerouslySetInnerHTML={{ __html: JSON.stringify(data) }}
  />
);

export default JsonSchema;
